/// Form template with elements
/// @group Form
.form-template {
  width: 80%;
  height: fit-content;
  padding: 1em;
  margin: 0 auto;
  display: flex;
  flex-direction: column;

  .section {
    margin: 1em 0;
    height: fit-content;
    display: flex;
    flex-direction: column;

    label {
      font: var(--textFormFieldName);
      margin: .5rem 0;
      letter-spacing: .1rem;
    }

    input {
      border: .1rem solid var(--colorGrey);
      border-radius: 4px;
      background: none;
      font: var(--textFormField);
      padding: .5rem .8rem;
      z-index: 10;

      &:focus {
        border: none;
        border: .1rem solid var(--colorPrimary);
      }
    }

    .prompt {
      z-index: 1;
      margin: 0 auto;
      margin-top: -1.7rem;
      width: 95%;
      min-height: 1.5rem;
      font: var(--textFormFieldPrompt);
      color: var(--colorGrey);
    }
  }

  .buttonsWrapper {
    display: flex;
    align-items: baseline;
  }
}

/// Form alignment for left-to-right languages
/// @group Form
.formLeft {
  @extend .form-template;
  text-align: left;

  span {
    margin-left: 92%;
  }
}

/// Form alignment for right-to-left languages
/// @group Form
.formRight {
  @extend .form-template;
  text-align: right;

  label {
    font-size: .9rem !important;
  }

  input {
    direction: rtl;
  }
}

/// Message style
/// @group Form
.formMessage {
  margin: 1.5rem auto;
  // height: fit-content;
  margin: 0;
  height: 2rem;
  width: 100%;
  text-align: center;
  font: var(--textFormMessage);
  color: var(--colorAttention);
}

/// Loading indicator wrapper
/// @group Form
.formLoading {
  position: relative;
  height: 2rem;
  bottom: 2rem;
  margin: 0;
  display: flex;
  align-items: center;
}

.section {
  margin-bottom: var(--distStandard);
  height: fit-content;
  display: flex;
  flex-direction: column;

  // to avoid difference in height between select and input
  input {
    height: 2rem;
  }

  input,
  select,
  textarea {
    border: var(--border);
    border-radius: var(--borderRadius);
    background-color: var(--colorWhite);
    font: var(--textFormField);
    padding: .5rem .8rem;
    z-index: 10;

    &::placeholder {
      color: var(--colorGrey);
    }

    &:focus {
      border: none;
      border: .1rem solid var(--colorPrimary);
    }
  }

  // to avoid hiding down icon
  select {
    background: none;
  }

  // to avoid hiding the bottom right corner
  textarea {
    border-radius: var(--borderRadiusText);
  }

  .inline {
    height: fit-content;
    display: flex;
    align-items: center;

    select {
      padding-left: 2.7rem;
      width: 100%;
    }

    svg {
      position: absolute;
      width: 1.5rem;
      height: 1.5rem;
      padding: .2rem;
      grid-column: 1;
      grid-row: 1;
      margin-left: .8rem;
    }
  }

}

.sectionRTL {
  @extend .section;
  direction: rtl;

  select {
    padding-right: 2.7rem;
    padding-left: none;
  }

  svg {
    margin-left: none;
    margin-right: .8rem;
  }
}