/// Wrapper template
/// @group Buttons
.wrapper-template {
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

/// Vertical wrapper
/// @group Buttons
.vertical {
  @extend .wrapper-template;
  flex-direction: column;
}

/// Horizontal wrapper
/// @group Buttons
.horizontal {
  @extend .wrapper-template;
  flex-direction: row;
}

/// Horizontal wrapper right-to-left
/// @group Buttons
.horizontalRTL {
  @extend .horizontal;
  direction: rtl;
}

.enter {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-start;


  span {
    animation: var(--aniVerticalFlip);
    color: white;
    font: var(--textButtonEnter);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;

    letter-spacing: 1%;
    text-align: center;
    vertical-align: middle;
    opacity: 0;
    overflow: hidden;
    position: absolute;

    &:nth-child(1) {
      font: var(--textButtonEnterLtr);
      height: 95%;
    }

    &:nth-child(2) {
      animation-delay: 2.5s;
    }

    &:nth-child(3) {
      animation-delay: 5s;
    }
  }
}

.nav-template {
  position: absolute;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header {
  @extend .nav-template;
  top: 0;
  height: 10%;
  margin: 0 auto;
  width: 95%;
  justify-content: space-between;

  h1 {
    width: 80%;
    text-align: center;
    font: var(--textPageTitle);
    color: var(--colorWhite);
  }
}

.headerRTL {
  @extend .header;
  // direction: rtl;
  direction: rtl;

  svg {
    transform: rotate(180deg);
  }
}

.footer-template {
  @extend .nav-template;
  bottom: 0;
  height: 10%;
}

.footer {
  @extend .footer-template;
  justify-content: space-around;
}

.footerRTL {
  @extend .footer;
  direction: rtl;
}